.programs{
    min-height: 650px;
    background-color: #161616;
    text-align: center;
    padding-top: 81px;
    &__icon{
        display: block;
        margin: 0 auto;
    }
    &__title{
        margin-top: 46px;
        color: #fefefe;
        font-size: 22px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 1.1px;
    }
    &__text{
        margin-top: 30px;
        color: #fefefe;
        font-size: 12px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.6px;
    }
    .btn{
        margin-top: 33px;
    }
    .row{
        display: flex;
    }
}
@media (max-width:992px){
    .programs .row {
        display: flex;
        align-items: center;
    }
}
@media (max-width:768px){
    .programs {
        padding-bottom: 10px;
        padding-top: 10px;
    }
    .programs__title {
        margin-top: 20px;
    }
}
@media (max-width:767px){
    .programs__item{
        margin-top: 30px;
    }
    .programs__icon {
        width: 80px;
    }
}
