.slider{
    padding-top: 80px;
    min-height: 610px;
    background-color: #111111;
    position: relative;
    overflow: hidden;
    .slider__wraper{
        width: 1400px;
        margin: 0 auto;
    }
    .slick-arrow{
        position: absolute;
        z-index: 1;
        top: 44%;
        font-size: 0;
        width: 20px;
        height: 32px;
        border: 0;
        background-color: transparent;
    }
    .slick-next{
        right: 0;
        background-image: url(../img/right_arrow.png);
    }
    .slick-prev{
        left: 0;
        background-image: url(../img/left_arrow.png);
    }
    .slick-slide{
        display: flex;
        justify-content: center;
   }
   .slick-list{
        overflow: visible;
    }
    
}
@media (max-width:1400px){
    .slider{
        .slider__wraper{
            width: auto;
        }
    }
}
@media (max-width:1140px){
    .slider{
        .slider__wraper{
            width: 900px;
        }
    }
}
@media (max-width:900px){
    .slider{
        .slider__wraper{
            width: auto;
        }
    }
}
@media (max-width:799px){
    .slider{
        .slider__wraper{
            width: 400px;
        }
    }
}
@media (max-width:430px){
    .slider{
        .slider__wraper{
            width: auto;
        }
        .slick-arrow{
            top: 32%;
        }
        img{
            width: 260px;
        }
    }
}



