.contact{
    min-height: 515px;
    background: url(../img/bg__contact.png) center center/cover no-repeat;
    padding-top: 79px;
    &__text{
        width: 615px;
        margin: 0 auto;
        color: #ffffff;
        text-align: center;
        font-size: 18px;
        font-weight: 400;
        line-height: 30px;
    }
}
@media (max-width:662px){
    .contact__text {
        width: auto;
    }
}
@media (max-width:576px){
    .title__contact {
        font-size: 36px;
    }
    .contact__text {
        font-size: 14px;
    }
    .btn__contact {
        margin-top: 25px;
    }
    .contact {
        min-height: 330px;
    }
    .title {
        padding-bottom: 0;
        line-height: 35px;
    }
}
@media (max-width:367px){
    .contact{
        padding-bottom: 20px;
    }
}