@import "../libs/normalize.scss";
@import "../fonts/fontawose/all.min.css";
@import "../libs/bootstrap-grid.min.scss";
@import "base/fonts.scss";
*{
    font-family: "Lato";
    font-weight: 400;
    color: #fff;
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
}
.btn{
    width: 140px;
    height: 50px;
    border-radius: 5px;
    border: 2px solid #f80759;
    background-color: rgba(#ed213a,0);
    color: #fefefe;
    font-size: 12px;
    line-height: 24px;
    &:hover{
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
    }
    &__footer{
        width: 100px;
        height: 39px;
        border: 1px solid #555555;
        background-color: #111111;
    }
    &_header{
        width: 200px;
        height: 50px;
        border-radius: 5px;
        border: 1px solid #f80759;
        background-color: rgba(#ed213a,0);
        color: #fefefe;
        font-size: 16px;
        line-height: 24px;
    }
    &_price{
        border: 2px solid #222222;
        &:hover{
            border: 2px solid #222222;
            background-image: none;
        }
    }
    &__contact{
        display: block;
        margin: 0 auto;
        width: 200px;
        height: 50px;
        border-radius: 5px;
        background-color: #ed213a;
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
        margin-top: 79px;
    }
}
.brg{
    position: relative;
    z-index: 1;
    overflow: hidden;
    height: 30px;
    width: 33px;
    display: none;
    flex-direction: column-reverse;
    justify-content: space-between;
    .brg_tiem{
        border-radius: 4px;
        height: 5px;
        background-color: white;
        transition: all 0.3s ease;
    }
    .brg_tiem:first-child{
        height: 6px;
    }
}
.brg.active{
    .brg_tiem:nth-child(1){
        transform:translateY(-12px) rotate(-45deg);
    }
    .brg_tiem:nth-child(2){
        transform: translateX(-110%);
    }
    .brg_tiem:nth-child(3){
        transform:translateY(12px) rotate(45deg);
    }
}
.subtitle{
    text-align: center;
    color: #12c2e9;
    font-family: "Lato";
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0.9px;
    &_trainers{
        text-align: left;
    }
}
.title{
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    /* Text style for "W, elcome" */
    letter-spacing: 1.5px;
    padding-bottom: 29px;
    position: relative;
    &::before{
        content:"";
        width: 60px;
        height: 2px;
        position: absolute;
        bottom: 0;
        left: calc(50% - 30px);
        background-color: #ed213a;
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
    }
    &_trainers{
        text-align: left;
        &::before{
            left:0;
        }
    }
    &__contact{
        color: #ffffff;
        font-size: 48px;
        font-weight: 700;
        line-height: 24px;
        text-transform: uppercase;
        letter-spacing: 2.4px;
        &::before{
            display: none;
        }
    }
}
.descr{
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    line-height: 26px;
}
.video{
    position: fixed;
    z-index: 2;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(black,0.9);
    display: none;
    justify-content: center;
    align-items: center;
}
.video.active{
    display: flex;
}
.slick-list{
    overflow: hidden;
}
.slick-track{
    display: flex;
    justify-content: space-between;
}
@import "block/header.scss";
@import "block/treaners.scss";
@import "block/team.scss";
@import "block/programs.scss";
@import "block/rewiews.scss";
@import "block/slider.scss";
@import "block/price.scss";
@import "block/news.scss";
@import "block/contact.scss";
@import "block/footer.scss";