.price{
    padding-bottom: 80px;
    min-height: 704px;
    background-color: #111111;
    &__item{
        margin-top: 10px;
        position: relative;
        border: 2px solid #222222;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &__title{
        margin-top: 33px;
        font-size: 24px;
        line-height: 24px;
        letter-spacing: 1.2px;
        span{
            font-size: 48px;
            font-weight: 800;
            letter-spacing: 2.4px;
        }
    }
    &__text{
        margin-top: 126px;
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
    }
    button{
        margin-top: 39px;
        margin-bottom: 45px;
    }
    &__line{
        position: absolute;
        top: 106px;
        padding-right: 1px;
        left: -6px;
        border-right: 5px solid #111111;
        border-left: 6px solid #111111;
        text-align: center;
        padding-top: 16px;
        width: 103%;
        height: 50px;
        background-color: #262424;
    }
}
.price__item.active{
    border: 3px solid #12c2e9;
    .price__line{
        padding-right: 2px;
        left: -5.5px;
        background-color: #ed213a;
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
        width: 103%;
        border-right: 4px solid #111111;
        border-left: 4px solid #111111;
    }
    button{
        background-color: #ed213a;
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
    }
}
@media (max-width:1200px){
    .price{
        padding-bottom: 0;
        min-height: 660px;
        &__line{
            position: absolute;
            top: 106px;
            padding-right: 1px;
            left: -3px;
            border-right: 2px solid #111111;
            border-left: 2px solid #111111;
            text-align: center;
            padding-top: 16px;
            width: 102%;
            height: 50px;
            background-color: #262424;
        }
    }
    .price__item.active{
        border: 3px solid #12c2e9;
        .price__line{
            padding-right: 2px;
            left: -3px;
            background-color: #ed213a;
            background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
            width: 102%;
            border-right: 2px solid #111111;
            border-left: 2px solid #111111;
        }
        button{
            background-color: #ed213a;
            background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
        }
    }
}
@media (max-width:992px){
    .price__line {
        left: -2px;
    }
    .price__item.active .price__line {
        left: -3px;
        width: 103%;
    }
}
@media (max-width:768px){
    .price__item.active .price__line {
        left: -3px;
        width: 101.5%;
    }
    .price__line {
        width: 101%;
    }
    .price {
        padding-bottom: 20px;
    }
}
@media (max-width:511px){
    .price__line {
        left: -1.8px;
    }
    .price__item.active .price__line {
        left: -2.8px;
    }
}
@media (max-width:476px){
    .price__item.active .price__line {
        left: -4px;
        width: 102%;
    }
    .price__line {
        left: -2px;
    }
}
@media (max-width:476px){
    .price__line{
        left: -2.9px;
        width: 101.8%;
    }
    .price__item.active .price__line {
        left: -4px;
        width: 103%;
    }
}
@media (max-width:343px){
    .price__line {
        left: -1.9px;
    }
}





