.footer{
    min-height: 555px;
    background-color: #111111;
    padding-top: 77px;
    &__text{
        margin-top: 43px;
        color: #bdc3c7;
        font-size: 16px;
        font-weight: 400;
        line-height: 30px;
    }
    &__subtitle{
        margin-top: 36px;
        color: #bdc3c7;
        font-size: 20px;
        font-weight: 700;
        line-height: 30px;
    }
    &__title{
        font-size: 24px;
        font-weight: 800;
        line-height: 30px;
    }
    &__info-li{
        display: block;
        padding-right: 27px;
        margin-top: 46px;
        position: relative;
        &:nth-child(2){
            &::before{
                content:'';
                position: absolute;
                background: url(../img/footer_2.png);
                top: calc(50% - 10px);
                left: -22px;
                width: 10px;
                height: 16px;
            }
        }
        &:nth-child(3){
            &::before{
                content:'';
                position: absolute;
                background: url(../img/footer_3.png);
                top: calc(50% - 5px);
                left: -25px;
                width: 18px;
                height: 15px;
            }
        }
        &:last-child{
            &::before{
                content:'';
                position: absolute;
                background: url(../img/footer_4.png);
                top: calc(50% - 10px);
                left: -26px;
                width: 18px;
                height: 18px;
            }
        }
        &::before{
            content:'';
            position: absolute;
            background: url(../img/footer_1.png);
            top: calc(50% - 13px);
            left: -22px;
            width: 13px;
            height: 20px;
        }
    }
    &__btn{
        margin-top: 47px;
        button{
            margin-right: 10px;
            margin-bottom: 8px;
        }
    }
    &__nav{
        height: 80px;
        background-color: #ed213a;
        background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
    }
    &__law{
        color: #ffffff;
        font-size: 16px;
        font-weight: 800;
        text-align: center;
        padding-top: 18px;
    }
    &__nav-ul{
        padding-left: 0;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items:center;
        justify-content: center;
        li{
            margin-top: 8px;
            margin-right: 30px;
            &:last-child{
                margin-right: 0;
            }
        }
    }
    &__info{
        padding-left: 17px;
    }
    &__nav-link{
        display: flex;
    }
    &__nav-contact{
        margin-right: 11px;
        &:last-child{
            margin-right: 0;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid #555555;
        &:hover{
            border: 1px solid #f80759;
        }
    }
}
.footer .nav__logo{
    margin-top: 0;
    &::before{
        left: 5px;
    }
}
.footer__law{
    padding-bottom: 21px;
}
@media(max-width:992px){
    .footer__info-li {
        margin-top: 30px;
        &:first-child{
            margin-top: 43px;
        }
    }
    .footer__info-li {
        padding-right: 0;
    }
    .footer {
        padding-top: 30px;
    }
    .footer__title-cenrer{
        margin-top: 10px;
        text-align: center;
    }
    .footer__btn {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 12px;
    }
}
@media(max-width:575px){
    .footer .nav__logo::before {
        left: -33px;
    }
    .col-12{
        text-align: center;
    }
    a{
        display: block;
        margin: 0 auto;
    }
    .footer .nav__logo {
        padding-left: 0;
    }
    .footer {
        padding-top: 10px;
    }
    .footer__nav-contact{
        margin-left: 0;
    }
    .footer__nav-link {
        display: flex;
        justify-content: center;
    }
    .footer__info-li:first-child {
        margin-top: 18px;
    }
    .footer__title{
        margin-top: 18px;
    }
    .footer__subtitle {
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .footer .nav__logo{
        display: inline-block;
    }
    .footer.nav__logo::before {
        left: -33px;
    }
    .footer__info-li{
        display: block;
    }
    .footer__info {
        padding-left: 0;
    }
    .footer__text {
        margin-top: 0px;
    }
}