.header{
    min-height:750px;
    background: url(../img/bg__header.png)center center/cover no-repeat;
    &__title{
        margin-top: 170px;
        color: #fefefe;
        font-size: 60px;
        font-weight: 800;
        text-transform: uppercase;
        span{
            color: #12c2e9;
            font-size: 48px;
            font-weight: 800;
        }
    }
    &__subtitle{
        margin-top: 20px;
        font-size: 16px;
        line-height: 24px;
    }
    .btn__wraper{
        margin-top: 24px;
        display: flex;
        button{
            margin-right: 20px;
            &:last-child{
                margin-right: 0;
            }
        }
    }
}
.nav{
    padding-top: 4px;
    &__logo{
        padding-left: 40px;
        color: #ffffff;
        font-size: 36px;
        font-weight: 800;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            background: url(../img/logo.png);
            width: 30px;
            height: 26px;
            bottom: 6px;
            left: 9px;
        }
        span{
            color: #f80759;
        }
    }
    &__ul{
        display: flex;
    }
    &__li{
        margin-right: 30px;
        &:last-child{
            margin-right: 0;
        }
    }
    &__link{
        color: #ffffff;
        font-size: 16px;
        font-weight: 400;
        &:hover{
            color: #f80759;
        }
    }
}
.wraper{
    width: 1140px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
@media (max-width:1200px){
    .brg{
        display: flex;
    }
    .nav__ul{
        padding-left: 0;
        transition: all 0.3s ease;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        li{
            margin-right: 0;
            margin-top: 40px;
            &:first-child{
                margin-top: 0;
            }
            a{
                font-size: 30px;
            }
        }
        top: -16px;
        left: -100%;
        position: fixed;
        z-index: 1;
        background-color: #161616;
        height: 100%;
        width: 100%;
    }
    .nav__ul.active{
        padding-left: 0;
        transition: all 0.3s ease;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        li{
            margin-right: 0;
            margin-top: 40px;
            &:first-child{
                margin-top: 0;
            }
            a{
                font-size: 30px;
            }
        }
        top: -16px;
        left: 0;
        position: fixed;
        z-index: 1;
        background-color: #161616;
        height: 100%;
        width: 100%;
    }
    .wraper{
        width: 960px;
    }
}
@media (max-width:992px){
    .header{
        min-height: 690px;
    }
    .wraper{
        width: 690px;
    }
    .header__title{
        font-size: 52px;
        span{
            font-size: 40px;
        }
    }
    .header__subtitle{
        font-size: 14px;
    }
    .btn_header{
        width: 170px;
        height: 45px;
    }
}
@media (max-width:767px){
    .header {
        min-height: 670px;
    }
    .wraper{
        width: 540px;
    }
    .header__title{
        font-size: 44px;
        span{
            font-size: 32px;
        }
    }
    .header__subtitle{
        font-size: 12px;
    }
    .btn_header{
        width: 160px;
        height: 45px;
    }
}
@media (max-width:576px){
    .header {
        min-height: 650px;
    }
    .wraper{
        width: 85%;
    }
    .header__title{
        text-align: center;
        margin-top: 135px;
    }
    .header__subtitle{
        text-align: center;
    }
    .btn__wraper{
        justify-content: center;
    }
}