.rewiews{
    padding-top: 79px;
    min-height: 665px;
    background: url(../img/bg__rewiews.png) center center/cover no-repeat;
    &__title{
        color: #12c2e9;
        font-size: 18px;
        font-weight: 600;
        line-height: 24px;
    }
    &__subtitle{
        font-size: 10px;
        line-height: 24px;
    }
    &__wrap{
        width: 1142px;
        margin: 0 auto;
    }
    &__slider{
        margin-top: 81px;
    }
    &__item{
        background-image: url(../img/rewiews_2.png);
        background-repeat: no-repeat;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 254px;
        width: 555px;
        height: 300px;
        background-color: #222222;
        &:nth-child(odd){
            background-image: url(../img/rewiews_1.png);
            margin-right: 32px;
        }
    }
    &__strar{
        margin-top: 29px;
    }
    &__text{
        margin-top: 20px;
    }
    .slick-dots{
        padding-left: 0;
        margin-top: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .slick-dots li{
        margin-right: 11px;
        &:last-child{
            margin-right: 0;
        }
    }
    .slick-dots li button{
        font-size: 0;
        width: 14px;
        height: 14px;
        border: 0;
        background-color: white;
        border-radius: 50%;
    }
    .slick-dots li.slick-active{
        button{
            color:#12c2e9 ;
            background-color: #12c2e9;
        }
    }
}
@media (max-width:1200px){
    .rewiews {
        padding-top: 48px;
    }
    .rewiews__title {
        color: #12c2e9;
        font-size: 16px;
        font-weight: 600;
        line-height: 8px;
    }
    .rewiews__text {
        margin-top: 8px;
        font-size: 14px;
    }
    .rewiews__strar {
        margin-top: 22px;
    }
}
@media (max-width:1140px){
    .slider {
        padding-top: 50px;
        min-height: 500px;
    }
}
@media (max-width:991px){
    .rewiews__slider {
        margin-top: 30px;
    }
    .rewiews {
        padding-top: 20px;
        min-height: 570px;
    }
    .rewiews{
        &__item{
            width: 555px;
            &:nth-child(odd){
                margin-right: 0;
            }
        }
    }
}
@media (max-width:450px){
    .rewiews__title {
        color: #12c2e9;
        font-size: 14px;
        font-weight: 600;
        line-height: 0px;
    }
    .rewiews__text {
        margin-top: 5px;
        font-size: 12px;
    }
}
@media (max-width:405px){
    .rewiews__title {
        color: #12c2e9;
        font-size: 12px;
        font-weight: 600;
        line-height: 12px;
    }
    .rewiews__text {
        margin-top: 5px;
        font-size: 10px;
    }
    .rewiews__strar {
        margin-top: 10px;
    }
    .rewiews__item{
        background-position: -40px center;
        padding-left: 196px;
    }
}