.news{
    min-height: 820px;
    background-color: #161616;
    padding-top: 79px;
    &__item{
        border: 2px solid #222222;
    }
    &__wraper{
        padding: 38px 19px 30px 19px;
    }
    &__name-link{
        color: #f80759;
        font-size: 12px;
        font-weight: 400;
        line-height: 24px;
    }
    &__title{
        margin-top: 15px;
        font-size: 18px;
        font-weight: 700;
        line-height: 24px;
    }
    &__text{
        margin-top: 28px;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
    }
    &__link{
        color: #f80759;
        font-size: 14px;
        font-weight: 800;
        line-height: 24px;
        margin-top: 18px;
        display: block;
    }
    &__likeText{
        font-size: 14px;
        font-weight: 600;
        line-height: 24px;
        color: #12c2e9;
    }
    &__like{
        padding-right: 20px;
        font-size: 14px;
        font-weight: 600;
        line-height: 24px;
        color: #12c2e9;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            background: url(../img/heart_shape.png);
            width: 16px;
            height: 15px;
            top: 4px;
            left: 18px;
        }
    }
    &__item-wraper{
        display: flex;
        justify-content: space-between;
    }
    &__img{
        width: 100%;
    }
}
@media(max-width:1200px){
    .news__likeText {
        font-size: 10px;
    }
    .news__like {
        font-size: 12px;
    }
    .news__like::before {
        left: 15px;
    }
    .news__wraper {
        padding: 0px 12px 30px 12px;
    }
    .news__title {
        font-size: 16px;
    }
    .news__text {
        margin-top: 20px;
        font-size: 12px;
    }
}
@media(max-width:1200px){
    .news__title {
        font-size: 15px;
    }
    .contact{
        padding-top: 20px;
        min-height: 430px;
    }
}
@media(max-width:992px){
    .news__item{
        &:nth-of-type(1),&:nth-of-type(2){
            margin-bottom: 20px;
        }
    }
}