.trainers{
    padding-top: 80px;
    min-height: 760px;
    background-color: #111111;
    .row{
        justify-content: center;
    }
    .descr{
        margin-top: 81px;
    }
    &__text{
        margin-top: 39px;
        font-size: 14px;
        line-height: 26px;
    }
    &__link{
        display: block;
        margin-top: 39px;
        color: #ed213a;
        font-size: 14px;
        font-weight: 700;
        line-height: 26px;
    }
    .trainers__video{
        margin-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        img{
            width: 170px;
            height: 100px;
        }
    }
    &__movie{
        background-image: url(../img/slider_video_3.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        display: block;
        width: 100%;
        height: 400px;
        display: flex;
        align-items: center;
        justify-content: center;
        span{
            display: block;
            width: 53px;
            height: 53px;
            background-image: url(../img/play.png);
        }
    }
}
.img__video.active{
    border: 2px solid #ed213a;
    background-image: linear-gradient(to right, #e100ff 0%, #f80759 100%);
}
@media (max-width:1200px){
    .trainers__movie{
        height: 340px;
    }
    .trainers {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .title_trainers{
        text-align: center;
        &::before{
            left: calc(50% - 30px);
        }
    }
    .trainers__text{
        text-align: center;
    }
    .title__treaners::before{
        left: calc(50% - 30px)
    }
    .trainers .descr {
        margin-top: 25px;
    }
    .trainers__text {
        margin-top: 10px;
    }
    .trainers__link {
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
@media (max-width:692px){
    .trainers .trainers__video {
        margin-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .trainers .trainers__video img {
        width: 140px;
        height: 80px;
    }
}
@media (max-width:576px){
    .descr{
        font-size: 20px;
    }
    .trainers__text {
        font-size: 12px;
        line-height: 22px;
    }
    

.title {
    line-height: 27px;
    }
}