.team{
    min-height: 524px;
    background-color: #111111;
    &__wraper{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    &__item{
        margin-top: 20px;
        position: relative;
        overflow: hidden;
        margin-right: 20px;
        &:last-child{
            margin-right: 0;
        }
    }
    &__descr{
        position: absolute;
        transition: all 0.3s ease;
        bottom: -44px;
        left:calc(50% - 54.5px);
    }
    &__title{
        text-align: center;
        color: #ffffff;
        font-size: 18px;
        font-weight: 700;
        line-height: 24px;
        letter-spacing: 0.9px;
    }
    &__spetialization{
        text-align: center;
        color: #12c2e9;
        font-size: 12px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.6px;
    }
}
.team__item{
    height: 298px;
}
.team__item.active{
    box-shadow: 0px 0px 7px #ed213a;
    .team__descr{
        bottom:25px;
    }
}
@media (max-width:1140px){
    .team{
        padding-bottom: 28px;
    }
 }
 @media (max-width:430px){
    .team__item{
        margin-right: 0;
    }
 }
 @media (max-width:440px){
    .team__item:last-child{
        margin-right: 20px;
     }
 }
